@import "../../../sass/variables";
@import "../../../sass/mixins";

.viewFriendsBlock {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  @include scroll();
  @media screen and (max-width: 980px) {
    flex-wrap: nowrap;
    width: 100%;
    height: auto;
    overflow-x: scroll;
    justify-content: unset;
  }

  .oneFriendContainer {
    border-radius: 12px;
    width: 40%;
    height: 300px;
    display: flex;
    margin: 10px;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    box-shadow: 15px 15px 20px rgba(0, 0, 0, 0.04);
    @media screen and (max-width: 980px) {
      width: 200px;
      flex: 0 0 auto;
    }

    .photo {
      width: 160px;
      height: 160px;
      border-radius: 80px;
    }

    .name {
      width: 100%;
      height: 30px;
      text-align: center;
      @include text($mainTextStyle, 1rem, 1.2);
      font-weight: 600;
      color: black;
    }

    .btn {
      width: 80%;
      height: 60px;
      display: flex;
      justify-content: space-evenly;
      align-items: center;

      .item {
        border: 1px solid transparent;
        border-radius: 1.25rem;
        width: 40px;
        height: 40px;
        background: gainsboro;
        @include text($secondTextStyle, 14px, 26px);
        color: black;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }


    .viewProfileBtn {
      margin-bottom: 10px;
      @include button(40px, 80%);

    }
  }
}